import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/bonos',
    name: 'Home',
    component: Home
  },
  {
    path: '/calculator',
    name: 'Calculator',
    component: () => import(/* webpackChunkName: "calculator" */ '../views/Calculator.vue')
  },
  {
    path: '/comparator',
    name: 'Comparator',
    component: () => import(/* webpackChunkName: "comparator" */ '../views/Comparator.vue')
  },
  {
    path: '/cartera',
    name: 'Cartera',
    component: () => import(/* webpackChunkName: "cartera" */ '../views/Cartera.vue')
  },
  {
    path: '/',
    name: 'Home Page',
    component: () => import(/* webpackChunkName: "landing" */ '../views/Landing.vue')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
