<template>
  <div class="home">
    <img alt="Vue logo" src="../assets/logo.png">
    <div>
      <BondList :forsafe="true" 
        :bonos="$store.state.lista_bonos" 
        @compare="bondlistAction(0, $event)"  
        @delete="bondlistAction(1, $event)" 
        @save="saveBonoCarteraModal($event)"/>
    </div>

    <div v-show="bono" style="background-color: #0004; position: fixed; left:0; top: 0; width: 100%; height: 100%"></div>
    <div class="popup" v-if="bono">
      <h4>Guardar Bono</h4>
      <h6>Por favor, ingrese la fecha de adquisición del bono:</h6>
      <input type="date" v-model="bono.date" style="display:block; width: 80%; margin: auto; margin-bottom: 4px">
      <button @click="saveBonoCartera">Guardar</button>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import BondList from '../components/BondList.vue'

export default {
  name: 'Home',
  data: ()=>({
    bono : null,
    index : null
  }),
  components: {
    BondList
  },
  methods:{
    bondlistAction: function(type, event){
      let aux_arr = [];
      switch (type) {
        case 0:
          this.$store.dispatch('pre_load');

          event.forEach((element, index) => {
            if(element) aux_arr.push(this.$store.state.lista_bonos[index]);
            });

          this.$store.state.aux_bonos = aux_arr;
          this.$router.replace('/comparator');
        break;
        case 1:
          this.$store.dispatch('pre_load');

          event.forEach((element, index) => {
            if(!element) aux_arr.push(this.$store.state.lista_bonos[index]);
            });

          this.$store.state.aux_bonos = aux_arr;
          this.$store.dispatch('post_bonos');
        break;
      }
    },
    saveBonoCarteraModal(event){
      console.log(event);

      this.bono = this.$store.state.lista_bonos[event];
      this.bono.date = new Date().toDateString();

      this.index = event;
    },
    saveBonoCartera(event){
      this.$store.dispatch('pre_load');
      this.$store.state.aux_cartera.push(this.bono);
      this.$store.state.aux_bonos.splice(this.index, 1);
      this.$store.dispatch('post_bonos');

      this.bono = null;
      this.index = null;
    }
  },
  mounted(){
  }
}
</script>

