import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    current_user: {
      logged : false
    },
    lista_bonos:[
    ],
    cartera_bonos:[
    ],
    aux_bonos:[
    ],
    aux_cartera:[
    ],
    loading : false
  },
  mutations: {
    
  },
  actions: {
    pre_load (state){
      state.state.aux_bonos = [...state.state.lista_bonos];
      state.state.aux_cartera = [...state.state.cartera_bonos];
    },
    post_bonos( state ){
      state.state.loading = true;
      let url = "https://upc-finance-app-backend.herokuapp.com/users/" + state.state.current_user.uid;

      fetch(url,{
        method: 'POST',
        body: JSON.stringify(
          { 
            bonos: state.state.aux_bonos,
            cartera : state.state.aux_cartera
          }),
        headers:{'Content-Type': 'application/json'}
      }).then(response => response.json()).then(data =>{
        state.state.lista_bonos = data.bonos;
        state.state.cartera_bonos = data.cartera;
        state.state.loading = false;
      });

    },
    get_bonos( state ){
      state.state.loading = true;
      let url = "https://upc-finance-app-backend.herokuapp.com/users/" + state.state.current_user.uid;

      fetch(url).then(response => response.json()).then(data => {
        state.state.lista_bonos = data.bonos;
        state.state.cartera_bonos = data.cartera;
        state.state.loading = false;
      });
    }
  },
  modules: {
  }
})
