<template>
    <div class="container">
        <div class="bono-list">
            <div class="bono-item" v-for="(bono, index) in bonos" :key="index">
                <div style="display:flex; padding: 0 12px">
                    <input style="margin: auto" type="checkbox" v-model="selected[index]" v-on:change="check(index, $event)">
                </div>
                <div style="display:flex; flex-direction: column; text-align: left">
                    <h4 style="margin-bottom: 8px">{{bono.nombre}}</h4>
                    <h6>valor del cupón: {{bono.valor_actual}}</h6>
                    <h6>precio del cupón: {{bono.precio_actual}}</h6>
                </div>
                <div style="display:flex; text-align: right; justify-content: flex-end">
                    <span class="vna">{{bono.vna}}</span>
                    <a class="sbtn" href="#" v-show="forsafe" @click="$emit('save', index)"><i class="fas fa-save"></i></a>
                    <a class="sbtn" href="#" v-show="forview" @click="$emit('view', index)"><i class="far fa-calendar-alt"></i></a>
                </div>
            </div>
        </div>
        <div class="danger" v-show="active > 4">no es recomendable compara más de 4 Bonos a la vez, esta app no garantiza que la app pueda mostrar la información correctamente</div>
        <div style="padding: .5rem; justify-content: space-between">
            <button :disabled="active < 1" @click="$emit('compare', selected)" v-text="active > 1 ? 'Comparar':'Examinar'"></button>
            <button :disabled="active < 1" @click="$emit('delete', selected)">Eliminar</button>
        </div>
    </div>
</template>

<script>
export default {
    name:'BondList',
    data: ()=>({
        selected:[],
        active: 0,
        msg : ''
    }),
    props:{
        bonos: Array,
        forsafe :{
            type : Boolean,
            default : false
        },
        forview : {
            type : Boolean,
            default : false
        }
    },
    methods:{
        check(index, event){
            let a = this.selected.reduce((a,b)=>{
                return a + b;
                });

            this.active = a;
        }
    },
    mounted: function(){
        this.selected = this.bonos.map(()=>{return false});
    },
    watch:{
        bonos(){
            this.selected = this.bonos.map(()=>{return false});
            this.active = 0;
        }
    }
}
</script>

<style>
.container{
    max-width: 400px;
    margin:auto;
}
.danger{
    color: #f24;
    font-size: 10px;
}
.bono-list{
    overflow: auto;
    overflow-x: hidden;
    border:solid;
    background-color: #fff;
    margin-bottom: 4px;

    min-height: 400px;
}
.bono-item{
    border: none;
    background-color: #efe;
    display:flex;
    border-radius: 8px;
    
    box-shadow: inset -1px -1px 4px #0000002f;

    padding: .5rem;
}
.bono-item > div:last-child{
    flex:1
}
.bono-item h4, .bono-item h6{
    margin: 0;
}
.bono-item .label {
    display:block; margin: auto; margin-right: 0; font-size: 65%;
}
.bono-item > div .vna{
    box-shadow:         inset 1px 1px 4px #0000002f;
    font-size : 14px;
    min-width: 48px;
    padding: 8px 16px; background-color: #cfc; border-radius: 4px; font-weight: 700;
    margin: auto; display: block; margin-right: 4px;
}
.bono-item > div .sbtn{
    margin-top: auto;
    margin-bottom: auto;
    color: inherit;

    box-shadow:         inset 1px 1px 2px #ffff,
                        inset -1px -1px 4px #0003;
    padding: 8px 12px; background-color: #cfc; border-radius: 4px;
}

.bono-item > div .sbtn:active{
    background-color: #cfc;
    box-shadow:         inset 1px 1px 4px #0004;
}
</style>