<template>
    <div class="user-tab">
        <div class="loading" style="float: left" v-show="$store.state.loading">
            loading
        </div>
        <div v-if="!$store.state.current_user.logged">
            <form v-on:submit.prevent @submit="login">
                <div>
                    <label>Correo: </label>
                    <input name="uname" type="email">
                </div>
                <div>
                    <label>Contraseña: </label>
                    <input name="passw" type="password">
                </div>
            <button type="submit">Enlazar</button>
            </form>
        </div>
        <div v-else>{{$store.state.current_user.email}}</div>
    </div>
</template>

<script>
import {auth} from '@/main.js'

export default {
    name:'UserTab',
    data: ()=>({
    }),
    computed:{
    },
    mounted(){
    },
    methods:{
        login(e){
            const formData = new FormData(e.target);
            var vals = {};
            for (var pair of formData.entries()) {
                vals[pair[0]] = pair[1];
            }
            let url = "https://upc-finance-app-backend.herokuapp.com/user?" + "uname=" + vals.uname;

            this.$store.state.loading = true;

            fetch(url).then(response=>response.json()).then(data=>{
                if(!data){  
                    auth.createUserWithEmailAndPassword(vals.uname, vals.passw).then((uC) =>{                      
                        this.$store.state.current_user = uC.user;
                        this.$store.state.current_user.logged = true;

                        url =  "https://upc-finance-app-backend.herokuapp.com/users/" + this.current_user.uid;
                        fetch(url,{
                            method: 'POST',
                            body: JSON.stringify({
                                bonos:[],
                                cartera:[]
                                }),
                            headers:{
                                'Content-Type': 'application/json'
                            }
                        });

                        this.$store.dispatch('get_bonos');
                        this.$store.state.loading = false;
                    }).catch((e)=>{
                        alert("register error: " + e.message);
                        this.$store.state.loading = false;
                    });
                }else{
                    auth.signInWithEmailAndPassword(vals.uname, vals.passw).then((uC)=>{                     
                        this.$store.state.current_user = uC.user;
                        this.$store.state.current_user.logged = true;
                        this.$store.state.loading = false;

                        this.$store.dispatch('get_bonos');
                    }).catch((e)=>{
                        alert("login error: " + e.message);
                        this.$store.state.loading = false;
                    });
                }
            });     
        }
    }
}
</script>

<style>
.loading{
    opacity: .75;
}
.user-tab{
  background-color: #acf;
  padding: .5rem;
  text-align: right;
}
.user-tab * {
    margin:auto;
    font-size: 10px;
    margin-left: 4px;
}
.user-tab > div:last-child div{
    display: inline-block;
}

@media(max-width: 600px) {
    .user-tab > div:last-child div{
        display: block;
    }
}
</style>